import { mask } from "vue-the-mask";
import { validationMixin } from "vuelidate";
import getBrands from "@/services/card-info/utils/get-brands";
import { commonMixin, validatorsMixin, moveCaretMixin, helpersMixin } from "@/mixins";
import clickOutside from "@/utils/click-outside-directive";
import VueBankCardTooltip from "./VueBankCardTooltip";
export default {
  name: "VueBankCardBase",
  components: {
    VueBankCardTooltip: VueBankCardTooltip
  },
  directives: {
    mask: mask,
    clickOutside: clickOutside
  },
  mixins: [commonMixin, validationMixin, validatorsMixin, moveCaretMixin, helpersMixin],
  props: {
    isNew: Boolean,
    cardInfo: {
      type: Object,
      default: null
