    brandsPlaceholder: function brandsPlaceholder() {
      var brands = Object.values(getBrands());
      var aliases = ["visa", "master-card", "maestro", "mir"];
      return brands.filter(function (brand) {
        return aliases.includes(brand.alias);
      });
    }
  },
